@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.login-container{
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.logo{
  margin-left: 80px;
}

.login-container section.form{
  width: 100%;
  max-width: 350px;
  margin-right: 30px;
}

.login-container section.form form{
  margin-top: 100px;
}

.login-container section.form form h1{
  font-size: 32px;
  margin-bottom: 32px;
  color: #FFF;
  font-weight: 700;
}

.login-container section.form label{
  display:flex;
  justify-content: flex-start;
  margin:5px;
  font-weight:bold;
  color: #FFF;
}


section .banner{
  width: 596px;
  height: 574px;
}



.link section.form form a {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.3s;
}

.link section.form form a svg{
  margin-right: 8px;
}

.link section.form form a:hover{
  opacity: 0.8;
}

.ancora{
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #FFF;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.3s;
}

.ancora section.form form a svg{
  margin-right: 8px;
}

.ancora section.form form a:hover{
  opacity: 0.8;
}


@media only screen and (max-width: 1000px) {
  .login-container{
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 20px;
  
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }  
  .logo{
    margin: 10px 80px;
  }

  .ordernacao{
    order: -1;
  }

  section .banner{
    margin-top:80px;
    width: 400px;
    height: 200px;
    order: -1;
  }
  .login-container section.form form a{
    margin-bottom: 20px;
  }
}
